const ActiveSection = () => {
  return (
    <SectionContainer id="home">
      <SectionBackground>
        <BackgroundVedio autoPlay loop muted src={Video} type="vedieo/mp4" />
        {/* <Spline scene="https://prod.spline.design/fkEJWq0QuvVeb23Y/scene.splinecode"></Spline> */}
      </SectionBackground>
      <SectionContent>
        <h2
          className="animate__animated animate__bounce"
          style={{ color: "white" }}
        >
          Kawul`
        </h2>
        <SectionItem>
          <TypeWriter
            options={{
              autoStart: true,
              loop: true,
            }}
            onInit={(TypeWriter) => {
              TypeWriter.typeString("Don't Squander It Thinking. Do!")
                .pauseFor(2000)
                .deleteAll()
                .typeString("")
                .pauseFor(2000)
                .deleteAll()
                .typeString("")
                .pauseFor(2000)
                .deleteAll()
                .typeString("")
                .pauseFor(2000)
                .deleteAll()
                .typeString("~~")
                .pauseFor(2000)
                .deleteAll()
                .start();
            }}
          ></TypeWriter>
        </SectionItem>
        <IconLink
          to="technical"
          smooth={true}
          duration={500}
          spy={true}
          exact={true}
          offset={-80}
        >
          {/* <SectionIcon> */}
          <BiChevronsDown style={{ color: "white" }} />
          {/* </SectionIcon> */}
        </IconLink>
      </SectionContent>
    </SectionContainer>
  );
};

export default ActiveSection;
